.about{
  background-color: #00b0b0;
  padding: 50px 0;
  &__h1{
    font-family: 'Raleway-Bold',sans-serif;
    font-size: 90px;
    color: #ffffff;
    padding-bottom: 18px;
  }
  &__p{
    font-family: 'Raleway Light',sans-serif;
    font-size: 17px;
    color: #ffffff;
    line-height: 27px;
    word-spacing: 2px;
    padding-bottom: 25px;
  }
  &__text{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}
