.gallery{
  background-color: #ffe92f;
  padding-top: 100px;
  padding-bottom: 120px;
  &_description{
    width: 580px;
    margin: 0 auto;
    text-align: center;
    h1{
      font-family: "Raleway-Bold",sans-serif;
      font-size: 90px;
      color: #262626;
      line-height: 90px;
      padding-bottom: 30px;
    }
    p{
      font-family: "Raleway Light",sans-serif;
      font-size: 17px;
      line-height: 27px;
      padding-bottom: 50px;
    }
  }
  &__btn{
    font-family: "Raleway Light",sans-serif;
    margin: 50px auto 0;
    font-size: 20px;
    color: #ffffff;
    border: none;
    background-color: #5585a2;
    -webkit-box-shadow: 0 2px 5px rgba(0,0,0,.5);
    -moz-box-shadow: 0 2px 5px rgba(0,0,0,.5);
    box-shadow: 0 2px 5px rgba(0,0,0,.5);
    padding: 15px 43px;
    cursor: pointer;
  }
}
.gallery__container{
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}