.blog{
  background-color: #b07cf7;
  padding-bottom: 120px;
  .name{
    font-family: 'Raleway-Bold',sans-serif;
    font-size: 90px;
    color: #ffffff;
    text-align: center;
    padding-top: 100px;
    &__description{
      font-family: 'Raleway Light',sans-serif;
      font-size: 17px;
      line-height: 25px;
      color: #fff;
      text-align: center;
      padding-top: 30px;
      padding-bottom: 50px;
    }
  }
  &__description{
    position: absolute;
    bottom: 0;
    padding: 0 20px;
    color: #ffffff;
    &-about{
      font-family: 'Raleway-Bold',sans-serif;
      font-size: 20px;
      line-height: 25px;
    }
    &-time{
      font-family: 'Raleway Light',sans-serif;
      font-size: 13px;
      line-height: 25px;
      padding: 25px 0;
    }
  }
}
.header__blog{
  width: 570px;
  margin: 0 auto;
}
.blog__item-1,.blog__item-2,.blog__item-3{
  min-height: 310px;
  display: flex;
  background-size: cover;
  background-position: center;
  position: relative;
  -webkit-box-shadow: 0 10px 25px 2px rgba(0,0,0,0.59);
  -moz-box-shadow: 0 10px 25px 2px rgba(0,0,0,0.59);
  box-shadow: 0 10px 25px 2px rgba(0,0,0,0.59);
  margin: 0 10px
}
.border-time{
  display: inline-flex;
  min-width: 60px;
  min-height: 60px;
  background: rgba(59,64,74,0.5);
  border-radius: 8px;
  margin: 30px 30px;
}
.gradient{
  width: 100.1%;
  height: 100%;
  position: absolute;
  background-image: -webkit-linear-gradient(bottom, rgba(0,0,0,.8), rgba(255,255,255,.2));
  background-image: -moz-linear-gradient(bottom, rgba(0,0,0,.8), rgba(255,255,255,.2));
  background-image: -o-linear-gradient(bottom, rgba(0,0,0,.8), rgba(255,255,255,.2));
  background-image: -ms-linear-gradient(bottom, rgba(0,0,0,.8), rgba(255,255,255,.2));
  background-image: linear-gradient(to top, rgba(0,0,0,.8), rgba(255,255,255,.2));
}
.blog__time{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  color: #ffffff;
  &-number{
    font-family: 'Raleway-Bold',sans-serif;
    font-size: 32px;
  }
  &-month{
    font-family: 'Raleway-Heavy',sans-serif;
    font-size: 21px;
  }
}
.blog-btn{
  background-color: #f72b3b;
}