.header{
  background-image: url("../../img/header/header_banner.png");
  background-size: cover;
  position: relative;
  min-height: 100vh;
  background-position: center;
  padding-bottom: 200px;
}

.logo{
  font-family: 'Pacifico',fantasy;
  font-size: 50px;
  a{
    color: #262626;
    text-decoration: none;
  }
}
.navigation{
  display: flex;
  align-items: center;
  &__ul{
    font-family: 'Raleway-Bold',sans-serif;
    font-size: 16px;
    color: #262626;
    text-transform: uppercase;
    display: flex;
    li:not(:last-child){
      margin-right: 40px;
    }
    a{
      text-decoration: none;
      color: #262626;
    }
    li:before{
      content: '';
      height: 3px;
      width: 0;
      display: block;
      background-color: #ff6033;
      transition: 1s;
    }
   li:hover:before{
     width: 100%;
   }
    .active:before{
      content: '';
      height: 3px;
      width: 100%;
      display: block;
      background-color: #ff6033;
    }
  }
}

.header__container{
  width: 1180px;
  margin: 0 auto;
}
.header__wrapper{
  display: flex;
  justify-content: space-between;
}
.header__content{
  width: 370px;
  padding-top: 265px;
  h1{
    font-size: 60px;
    font-family: 'Raleway-Heavy', serif;
    padding-bottom: 15px;
  }
  p{
    font-family: 'Raleway Light',serif;
    font-size: 17px;
    line-height: 27px;
    padding-bottom: 35px;
  }
  button{
    font-family: 'Raleway Light',serif;
    color: #ffffff;
    border: none;
    cursor: pointer;
    background-color: rgb(255, 96, 51);
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.5);
    font-size: 20px;
    padding: 15px 26px;
  }
}
.scroll{
  text-align: center;
  position: absolute;
  bottom: 4%;
  left: 50%;
}