body{
  font-family: 'Raleway Light',sans-serif;
  font-size: 17px;
  color: #262626;
}
.d-flex{
  display: flex;
}

.gallery_1-min, .gallery_2-min, .gallery_3-min, .gallery_4-min, .gallery_5-min{
  background: url(../img/slide/sprites.png) no-repeat;
}

.gallery_1-min{
  background-position: 0 0;
  width: 788px;
  height: 568px;
}

.gallery_2-min{
  background-position: -406px -568px ;
  width: 378px;
  height: 268px;
}

.gallery_3-min{
  background-position: -2px -572px ;
  width: 378px;
  height: 268px;
}

.gallery_4-min{
  background-position: -858px 0;
  width: 578px;
  height: 408px;
}

.gallery_5-min{
  background-position: -858px -412px ;
  width: 578px;
  height: 408px;
}
.blog_1, .blog_2, .blog_3{
  background: url(../img/blog/blog.png) no-repeat;
}

.blog_1{
  background-position: -11px -354px ;
}

.blog_2{
  background-position: -517px 0;
}

.blog_3{
  background-position: 0 -3px ;
}




