.feedback{
  background-image: url("../../img/feedback-form/img_optimized.png");
  background-size: cover;
  background-position: center;
  padding-top: 100px;
  padding-bottom: 120px;
  .contact{
    width: 100%;
    text-align: center;
    font-family: 'Raleway-Bold',sans-serif;
    font-size: 90px;
    padding-bottom: 50px;
  }
  .contact__about{
    font-family: 'Raleway Light',sans-serif;
    width: 100%;
    text-align: center;
    font-size: 17px;
    line-height: 25px;
    padding-bottom: 50px;
  }
}
.feedback-form{
  input{
    display: block;
    padding: 15px 0;
    min-width: 500px;
    border: none;
    background: rgba(118,215,208,0.7);
    font-family: 'Raleway Light',sans-serif;
    font-size: 15px;
    line-height: 25px;
    color: #ffffff;
    text-decoration: greenyellow;
    outline: transparent;
    text-decoration: none;

  }
  input:focus{
    border-left: 5px solid #355b82;
    outline: transparent;
    text-decoration: none;
    background: rgba(0,196,196,1);
    font-family: 'Raleway-Bold',sans-serif;
  }
  input::placeholder{
    color: #ffffff;
    border: none;
    padding-left: 20px;
    outline: none;
    outline: transparent;
    text-decoration: none;
  }
  input:not(:last-child){
    margin-bottom: 15px;
  }
}
.text-aria{
  height: 250px;
  min-width: 500px;
  background: rgba(118,215,208,0.7);
  font-family: 'Raleway Light',sans-serif;
  font-size: 15px;
  line-height: 25px;
  border: none;

}
.text-aria::placeholder{
  margin-top: 10px;
  color: white;
  padding-left: 20px;
  padding-top: 10px;
}
.text-aria:focus{
  border-left: 5px solid #355b82;
  outline: transparent;
  text-decoration: none;
  background: rgba(0,196,196,1);
  font-family: 'Raleway-Bold',sans-serif;
}
.feedback-form-container{
  display: flex;
  width: 100%;
  justify-content: space-around
}
.our__address{
  font-family: 'Raleway Light',sans-serif;
  font-size: 20px;
  &-address{
    font-family: 'Raleway-Bold',sans-serif;
    font-size: 17px;
    a{
      font-family: 'Raleway Light',sans-serif;
      font-size: 20px;
      text-decoration: none;
      color: #262626;
      padding-top: 5px;
      display: block;
    }
    padding-bottom: 20px;
  }
}
