.footer{
  min-height: 90px;
  background-color: #262626;
  color: #ffffff;
  .container-flex{
    display: flex;
    justify-content: space-between;
    padding: 35px 0;
    align-items: center;
  }
  &__company{
    font-family: 'Raleway Light',sans-serif;
    font-size: 16px;
    line-height: 25px;
    color: #b9b9b9;
  }
  &__logo{
    font-family: 'Pacifico',sans-serif;
    font-size: 25px;
    color: #b9b9b9;
  }
  &__autor{
    font-family: 'Raleway Light',sans-serif;
    font-size: 15px;
    color: #b9b9b9;
    span{
      font-family: 'Raleway-Bold',sans-serif;
      font-size: 15px;
      color: #ff2b58;
    }
  }
}