.subscript{
  background-image: url("../../img/subscript/bg.jpg");
  background-position: right;
  background-size: contain;
  background-repeat: no-repeat;
  padding-top: 145px;
  padding-bottom: 160px;
  &__content{
    width: 830px;
  }
  &__description{
    width: 545px;
    h1{
      font-family: "Raleway-Bold",sans-serif;
      font-size: 40px;
      line-height: 27px;
      padding-bottom: 33px;
    }
    p{
      font-family: "Raleway Light",sans-serif;
      font-size: 17px;
      line-height: 27px;
      padding-bottom: 35px;
    }
  }
}
.form__email::placeholder{
  font-family: "Raleway Light",sans-serif;
  font-size: 15px;

}
.form__email{
  padding: 18px 0;
  padding-left: 15px;
  width: 70%;
  margin-right: 30px;
}
.subscript__btn{
  font-family: "Raleway Light",sans-serif;
  border: none;
  background-color: #96b92c;
  -webkit-box-shadow: 0 2px 5px rgba(0,0,0,.5);
  -moz-box-shadow: 0 2px 5px rgba(0,0,0,.5);
  box-shadow: 0 2px 5px rgba(0,0,0,.5);
  padding: 20px 47px;
  cursor: pointer;
  color: #ffffff;
  font-size: 20px;
}