.services{
  background-color: #70bfff;
  &__ul{
    width: 400px;
    padding-top: 180px;
    padding-bottom: 150px;
    li{
      height: 100px;
      background-color: #ffffff;
      -webkit-box-shadow: 0 2px 5px rgba(0,0,0,.5);
      -moz-box-shadow: 0 2px 5px rgba(0,0,0,.5);
      box-shadow: 0 2px 5px rgba(0,0,0,.5);
      transition: 1s;
      &:not(:last-child){
        margin-bottom: 20px;
      }
      a{
        font-family: 'Raleway-Bold',sans-serif;
        font-size: 20px;
        color: #262626;
        text-decoration: none;
        display: flex;
        width: 100%;
        height: 100%;
        justify-content: space-between;
      p{
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
      }
        .container__icon{
          width: 100px;
          display: flex;
          align-items: center;
          justify-content: center;
          border-left: 1px solid #9d9d9d;
          font-size: 40px;
        }
      }
      &:hover{
        -webkit-box-shadow: 0 4px 10px rgba(0,0,0,.7);
        -moz-box-shadow: 0 4px 10px rgba(0,0,0,.7);
        box-shadow: 0 4px 10px rgba(0,0,0,.7);
        background-color: #262626;
        a{
          color: #ffffff;
        }
      }
    }

  }
}
.bg-tomato{
  background-image: url("../../img/services/tomato.png");
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  h1{
    font-family: 'Raleway-Bold',sans-serif;
    font-size: 90px;
    color: #ffffff;
  }
}